import React from 'react'

const PhanQuyen =[]
const PhanQuyen_ =['Nhn vin qun tr','Qun tr vin','Qun l danh mc','Qun l bn hng','Qun l thu chi','Qun l thng k','Qun l cng n']

function Navbar({Tab,_Tab}) {
    const [TabAction,SetTabAction] = React.useState('')
    const [TabMenu,SetTabMenu] = React.useState(_Tab)
    const DuLieuPhanQuyen = JSON.parse(window.localStorage.getItem('dulieunguoidung'))
    PhanQuyen.push(DuLieuPhanQuyen[0].quyen)
    // console.log(DuLieuPhanQuyen)
    try {
      if(DuLieuPhanQuyen[0].danh_sach_quyen.split('-').length >=0){
        // console.log(DuLieuPhanQuyen[0].danh_sach_quyen.split('-'))
        DuLieuPhanQuyen[0].danh_sach_quyen.split('-').map(x=>
          PhanQuyen.push(x)
        )
      }
    } catch (error) {
            
      }
    // console.log(PhanQuyen)
    const [ChucNang,SetChucNang] = React.useState({
        ThongKeTongQuan : false,
        QuanTriVien : false,
        DanhMuc : false,
        QuanLy : false,
        NhapXuatTonKho : false,
        ThuChi : false,
        CongNo : false,
        ThongKeBaoCao : false,
    })
    // ***********************************************************************************
    //#region Phn quyn ngi dng
    const PhanQuyenChucNang = ()=>{
      if(PhanQuyen.indexOf('Nhn vin qun tr')>=0){
        SetChucNang({
          ThongKeTongQuan : true,
          QuanTriVien : true,
          DanhMuc : true,
          QuanLy : true,
          NhapXuatTonKho : true,
          ThuChi : true,
          CongNo : true,
          ThongKeBaoCao : true,
        })
      }
      else{
        SetChucNang({
          ThongKeTongQuan : PhanQuyen.indexOf('Nhn vin qun tr')>=0,
          QuanTriVien : PhanQuyen.indexOf('Qun tr vin')>=0,
          DanhMuc : PhanQuyen.indexOf('Qun l danh mc')>=0,
          QuanLy : PhanQuyen.indexOf('Qun l bn hng')>=0,
          NhapXuatTonKho : PhanQuyen.indexOf('Nhn vin qun tr')>=0,
          ThuChi : PhanQuyen.indexOf('Qun l thu chi')>=0,
          CongNo : PhanQuyen.indexOf('Qun l cng n')>=0,
          ThongKeBaoCao : PhanQuyen.indexOf('Qun l thng k')>=0,
        })
      }
    }
    //#endregion
    // ***********************************************************************************
    // console.log(PhanQuyen.indexOf('Qun tr vin')>=0)
    React.useEffect(()=>{
      SetTabMenu(_Tab)
      PhanQuyenChucNang()
    },[_Tab])
    return (
  <div>
   <aside id="left-panel" className="left-panel">
  <nav className="navbar navbar-expand-sm navbar-default">
    <div id="main-menu" className="main-menu collapse navbar-collapse">
      <ul className="nav navbar-nav">
        <li className={TabAction ==='Thng k tng quan' ? 'active' :''} hidden={!ChucNang.ThongKeTongQuan}>
          <a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Thng k tng quan');SetTabAction('Thng k tng quan')}}><i className="menu-icon fa fa-laptop"/>Thng k tng quan</a>
        </li>
        <li className={ChucNang.QuanTriVien !=='Thng k tng quan' ? 'active' :''} hidden={!ChucNang.QuanTriVien}>
          <a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Check vo ra nhn vin');SetTabAction('Thng k tng quan')}}><i className="menu-icon fa fa-laptop"/>Check vo ra nhn vin</a>
        </li>
        <li className={'menu-item-has-children dropdown'}   hidden={!ChucNang.QuanTriVien}>
          <a className="dropdown-toggle" data-toggle="dropdown" aria-expanded="false" style={{cursor:'pointer'}}> <i className="menu-icon fa fa-cogs" />Qun tr vin</a>
          <ul className="sub-menu children dropdown-menu"> 
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{SetTabAction('Qun tr vin');Tab('Qun l ti khon')}}>Qun l ti khon</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{SetTabAction('Qun tr vin');Tab('Phn quyn ngi dng')}}>Phn quyn ngi dng</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{SetTabAction('Qun tr vin');Tab('Qun l lng nhn vin theo thng')}}>Qun l lng nhn vin theo thng</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{SetTabAction('Qun tr vin');Tab('Thit lp lng nhn vin theo thng')}}>Thit lp lng nhn vin theo thng</a></li>
          </ul>
        </li>
        <li className="menu-item-has-children dropdown" hidden={!ChucNang.DanhMuc}>
          <a className="dropdown-toggle" data-toggle="dropdown" aria-expanded="false" style={{cursor:'pointer'}}> <i className="menu-icon fa fa-align-left" />Danh mc</a>
          <ul className="sub-menu children dropdown-menu"> 
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Loi i tng')}}>Loi i tng</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('i tng')}}>i tng</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('n v tnh')}}>n v tnh</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Loi hng')}}>Loi hng</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Mt hng')}}>Mt hng</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Ngnh hng')}}>Ngnh hng</a></li>
          </ul>
        </li>
        <li className="menu-item-has-children dropdown" hidden={!ChucNang.QuanLy}>
          <a className="dropdown-toggle" data-toggle="dropdown"  aria-expanded="false"> <i className="menu-icon fa fa-list-alt" />Qun l bn hng</a>
          <ul className="sub-menu children dropdown-menu"> 
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Qun l bn hng my pos')}}>Qun l bn hng my pos</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Danh sch ho n bn hng')}}>Danh sch ho n bn hng</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Qun l tr hng KH')}}>Qun l tr hng KH</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Danh sch ho n tr hng KH')}}>Danh sch ho n tr hng KH</a></li>
            {/* <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Test bar code')}}>Test bar code</a></li>
            <li><i className="" /><a href="ui-buttons.html">i tng</a></li>
            <li><i className="" /><a href="ui-buttons.html">Loi hng</a></li>
            <li><i className="" /><a href="ui-buttons.html">Mt hng</a></li>
            <li><i className="" /><a href="ui-buttons.html">Ngnh hng</a></li> */}
          </ul>
        </li>
        <li className="menu-item-has-children dropdown" hidden={!ChucNang.NhapXuatTonKho}>
          <a  className="dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"> <i className="menu-icon fa fa-list-alt" />Nhp xut</a>
          <ul className="sub-menu children dropdown-menu"> 
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Qun l nhp hng NCC')}}>Qun l nhp hng NCC</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Danh sch ho n nhp')}}>Danh sch ho n nhp</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Qun l tr hng NCC')}}>Qun l tr hng NCC</a></li>
            <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Danh sch ho n tr hng NCC')}}>Danh sch ho n tr hng NCC</a></li>
          </ul>
        </li>
        <li className="menu-item-has-children dropdown" hidden={!ChucNang.ThuChi}>
          <a href="#" className="dropdown-toggle" data-toggle="dropdown" aria-expanded="false"> <i className="menu-icon fa fa-xing-square" />Thu chi</a>
          <ul className="sub-menu children dropdown-menu"> 
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Phiu thu')}}>Phiu thu</a></li>
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Phiu chi')}}>Phiu chi</a></li>
          </ul>
        </li>
        <li className="menu-item-has-children dropdown" hidden={!ChucNang.CongNo}>
          <a href="#" className="dropdown-toggle" data-toggle="dropdown" aria-expanded="false"> <i className="menu-icon fa fa-xing" />Cng n</a>
          <ul className="sub-menu children dropdown-menu"> 
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Thanh ton NCC')}}>Thanh ton NCC</a></li>
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Cng n NCC')}}>Cng n NCC</a></li>
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Thanh ton KH')}}>Thanh ton KH</a></li>
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Cng n KH')}}>Cng n KH</a></li>
          </ul>
        </li>
        <li className="menu-item-has-children dropdown" hidden={!ChucNang.ThongKeBaoCao}>
          <a href="#" className="dropdown-toggle" data-toggle="dropdown" aria-expanded="false"> <i className="menu-icon fa fa-table" />Thng k bo co</a>
          <ul className="sub-menu children dropdown-menu">
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Thng k bn mt hng')}}>Thng k bn mt hng</a></li>
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Thng k nhp mt hng')}}>Thng k nhp mt hng</a></li>
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Thng k banking bn hng')}}>Thng k banking bn hng</a></li>
          <li><i className="" /><a style={{cursor:'pointer'}} onClick={async ()=>{Tab('Cht lng nhn vin')}}>Cht lng nhn vin</a></li>
            {/* <li><i className="fa fa-table" /><a href="tables-data.html">Data Table</a></li> */}
          </ul>
        </li>
      </ul>
    </div>{/* /.navbar-collapse */}
  </nav>
</aside>

        </div>
    )
}

export default Navbar

// ***********************************************************************************