/** @format */

import React from "react";
import DashBoard from "./ThongKe/DashBoard";
import PhanQuyenNguoiDung from "./QuanTriVien/PhanQuyenNguoiDung";
import QuanLyTaiKhoan from "./QuanTriVien/QuanLyTaiKhoan";
import ThongTinNguoiDung from "./QuanTriVien/ThongTinNguoiDung";
import QuanLyLuongNhanVien from "./QuanTriVien/QuanLyLuongNhanVien"
import ThietLapLuongNhanVien from './QuanTriVien/ThietLapLuongNhanVien'

import LoaiDoiTuong from "./DanhMuc/LoaiDoiTuong";
import DoiTuong from "./DanhMuc/DoiTuong";
import DonViTinh from "./DanhMuc/DonViTinh";
import LoaiHang from "./DanhMuc/LoaiHang";
import NganhHang from "./DanhMuc/NganhHang";
import MatHang from "./DanhMuc/MatHang";

import QuanLyBanHangPOS from "./QuanLyNghiepVu/QuanLyBanHangPOS";
import TestBarCode from "./QuanLyNghiepVu/testbarcode";
import QuanLyTraHangKH from "./QuanLyNghiepVu/QuanLyTraHangKH";
import DSHoaDonBan from "./QuanLyNghiepVu/DSHoaDonBan";
import DSHoaDonTraHang from "./QuanLyNghiepVu/DSHoaDonTraHang";

import QuanLyNhapHangNCC from "./NhapXuatHang/QuanLyNhapHangNCC";
import DSHoaDonNhap from "./NhapXuatHang/DSHoaDonNhap";
import QuanLyTraHangNCC from "./NhapXuatHang/QuanLyTraHangNCC";
import DSTraHangNCC from './NhapXuatHang/DSTraHangNCC'

import PhieuThu from "./ThuChi/PhieuThu";
import PhieuChi from "./ThuChi/PhieuChi";
import ThanhToanNCC from "./CongNo/ThanhToanNCC";
import ThanhToanKH from "./CongNo/ThanhToanKH";

import ThongKeBanMatHang from "./ThongKeBaoCao/ThongKeBanMatHang";
import ThongKeNhapMatHang from "./ThongKeBaoCao/ThongKeNhapMatHang";
import ThongKeBankingBanHang from "./ThongKeBaoCao/ThongKeBankingBanHang";
import CheckVaoRaNhanVien from "./CheckVaoRaNhanVien";
import ChotLuongNhanVien from "./ThongKeBaoCao/ChotLuongNhanVien";
import TichDiemKhachHang from "./QuanTriVien/TichDiemKhachHang"


function Body({ Tab }) {
  switch (Tab) {
    case "Check vo ra nhn vin":
      return (
        <React.Fragment>
          <CheckVaoRaNhanVien></CheckVaoRaNhanVien>
        </React.Fragment>
      );
    //#region Qun tr vin
    case "Thng k tng quan":
      return (
        <React.Fragment>
          <DashBoard></DashBoard>
        </React.Fragment>
      );
    case "":
      return (
        <React.Fragment>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );
    case "Qun l ti khon":
      return (
        <React.Fragment>
          <QuanLyTaiKhoan></QuanLyTaiKhoan>
        </React.Fragment>
      );
    case "Phn quyn ngi dng":
      return (
        <React.Fragment>
          <PhanQuyenNguoiDung></PhanQuyenNguoiDung>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );
    case "Thng tin ngi dng":
      return (
        <React.Fragment>
          <ThongTinNguoiDung></ThongTinNguoiDung>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );
    case "Qun l lng nhn vin theo thng":
      return (
        <React.Fragment>
          <QuanLyLuongNhanVien></QuanLyLuongNhanVien>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );

      case "Thit lp lng nhn vin theo thng":
        return (
          <React.Fragment>
            <ThietLapLuongNhanVien></ThietLapLuongNhanVien>
            <div style={{ width: "100%", height: "100vh" }}></div>
          </React.Fragment>
        );
    //#endregion

    //#region Danh mc
    case "Loi i tng":
      return (
        <React.Fragment>
          <LoaiDoiTuong></LoaiDoiTuong>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );
    case "i tng":
      return (
        <React.Fragment>
          <DoiTuong></DoiTuong>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );
      case "n v tnh":
        return (
          <React.Fragment>
            <DonViTinh></DonViTinh>
            <div style={{ width: "100%", height: "100vh" }}></div>
          </React.Fragment>
        );
        case "Loi hng":
          return (
            <React.Fragment>
              <LoaiHang></LoaiHang>
              <div style={{ width: "100%", height: "100vh" }}></div>
            </React.Fragment>
          );
        case "Ngnh hng":
          return (
            <React.Fragment>
              <NganhHang></NganhHang>
              <div style={{ width: "100%", height: "100vh" }}></div>
            </React.Fragment>
          );
        case "Mt hng":
          return (
            <React.Fragment>
              <MatHang></MatHang>
              <div style={{ width: "100%", height: "100vh" }}></div>
            </React.Fragment>
          );
    //#endregion

    //#region Qun l nghip v   Qun l bn hng my pos
    case "Qun l bn hng my pos":
      return (
        <React.Fragment>
            <QuanLyBanHangPOS></QuanLyBanHangPOS>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );
      case "Danh sch ho n bn hng":
        return (
          <React.Fragment>
              <DSHoaDonBan></DSHoaDonBan>
            <div style={{ width: "100%", height: "100vh" }}></div>
          </React.Fragment>
        );
      
      case "Qun l tr hng KH":
        return (
          <React.Fragment>
              <QuanLyTraHangKH></QuanLyTraHangKH>
            <div style={{ width: "100%", height: "100vh" }}></div>
          </React.Fragment>
        );
        case "Danh sch ho n tr hng KH":
          return (
            <React.Fragment>
                <DSHoaDonTraHang></DSHoaDonTraHang>
              <div style={{ width: "100%", height: "100vh" }}></div>
            </React.Fragment>
          );
      case "Test bar code":
        return (
          <React.Fragment>
              <TestBarCode></TestBarCode>
            <div style={{ width: "100%", height: "100vh" }}></div>
          </React.Fragment>
        );
    //#endregion
  
    //#region Nhp xut hng
    case "Qun l nhp hng NCC":
      return (
        <React.Fragment>
            <QuanLyNhapHangNCC></QuanLyNhapHangNCC>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );
      case "Danh sch ho n nhp":
        return (
          <React.Fragment>
              <DSHoaDonNhap></DSHoaDonNhap>
            <div style={{ width: "100%", height: "100vh" }}></div>
          </React.Fragment>
        );
        case "Qun l tr hng NCC":
          return (
            <React.Fragment>
                <QuanLyTraHangNCC></QuanLyTraHangNCC>
              <div style={{ width: "100%", height: "100vh" }}></div>
            </React.Fragment>
          );

          case "Danh sch ho n tr hng NCC":
            return (
              <React.Fragment>
                  <DSTraHangNCC></DSTraHangNCC>
                <div style={{ width: "100%", height: "100vh" }}></div>
              </React.Fragment>
            );
    //#endregion


    //#region ThuChi
    case "Phiu thu":
      return (
        <React.Fragment>
            <PhieuThu></PhieuThu>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );
      case "Phiu chi":
        return (
          <React.Fragment>
              <PhieuChi></PhieuChi>
            <div style={{ width: "100%", height: "100vh" }}></div>
          </React.Fragment>
        );

    //#endregion

    // #region CongNo
    case "Thanh ton NCC":
      return (
        <React.Fragment>
            <ThanhToanNCC></ThanhToanNCC>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      );
      case "Thanh ton KH":
        return (
          <React.Fragment>
              <ThanhToanKH></ThanhToanKH>
            <div style={{ width: "100%", height: "100vh" }}></div>
          </React.Fragment>
        );
    // #endregion 


    //#region Thng k
    case "Thng k bn mt hng":
      return(
        <React.Fragment>
          <ThongKeBanMatHang></ThongKeBanMatHang>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      )

    case "Thng k nhp mt hng":
      return(
        <React.Fragment>
          <ThongKeNhapMatHang></ThongKeNhapMatHang>
          <div style={{ width: "100%", height: "100vh" }}></div>
        </React.Fragment>
      )
      case "Thng k banking bn hng":
        return(
          <React.Fragment>
            <ThongKeBankingBanHang></ThongKeBankingBanHang>
            <div style={{ width: "100%", height: "100vh" }}></div>
          </React.Fragment>
        )

      case "Cht lng nhn vin":
        return (
          <React.Fragment>
            <ChotLuongNhanVien></ChotLuongNhanVien>
          </React.Fragment>
        );
        
      case "Tch im ngi dng":
        return (
          <React.Fragment>
            <TichDiemKhachHang></TichDiemKhachHang>
          </React.Fragment>
        );
    //#endregion
  }
}

export default Body;
